* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.home {
  width: 100%;
  height: 100vh;
  background-color: #9f86c0;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 50px;
}

.landing__container {
  display: flex;
  width: 80%;
  text-align: center;
  align-items: center;
  gap: 50px;
}

.landing__target {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: xx-large;
  gap: 40px;
}

.landing__socialMedia {
  display: flex;
  width: 50%;
  background-color: #d4e09b;
  border-radius: 25px;
}

.landing__socialMedia > li {
  list-style-type: none;
  flex: 1;
}

.landing__target2 {
  display: flex;
  align-items: flex-end;
}

.landing__dino {
  width: 395px;
  height: 399px;
  display: flex;
  border-radius: 15px;
}
